<template>
    <div>
        <h1 class="main-title"></h1>
        <Row>
            <Col class="index-list" v-for="(item,index) in nowList" :key="item.id" :xs="{ span: 24, offset:0 }" :lg="{ span: 6, offset: index%3===0?0:3 }">
                <img :src="item.picSrc | imagePath">
                <h3>{{item.title}}<Button @click="follow(item.id)" class="index-list-btn" type="primary"></Button></h3>
            </Col>
        </Row>
    </div>
</template>

<script>
	import requestObj from '../../api/api';
	export default {
		name: "likeList",
		data(){
			return {
				nowList:[],
			};
		},
		created(){
			this.initNowData();
		},
		methods:{
			follow(id){
				var self=this;
				requestObj({
					url:'userFollow.do',
					method:'GET',
					params:{
						filmid:id,
					},
					success:function (res){
						self.$Message.success('');
						self.initNowData();
					},
					fail:function (err){
						console.log(err);
					},
				});
			},
			initNowData(){
				var self=this;
				requestObj({
					url:'getUserFollow.do',
					method:'GET',
					params:{
						pageCurrent:1,
						pageSize:10,
					},
					success:function (res){
						var data=res.data.data;
						self.nowList=data;
					},
					fail:function (err){
						console.log(err);
					},
				});
			},
		},
	}
</script>

<style scoped>

</style>


